.event-snippet {
  display: flex;
  justify-content: space-between;
  margin: var(--m-sm-spacing)
}

.event-details {
  width: 50%
}

.event-details a {
  text-decoration: none;
  color: var(--black)
}

.event-title {
  word-break: keep-all;
}

.event-picture{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 150px;
}

.event-image{
  width: 100%;
  border-radius: 10px;
}

.event-like {
  display: flex;
  gap: 5px;
  align-self: flex-end;
}

.event-like p {
  text-align: right;
}
.event-like span {
  text-align: right;
  width: 30px;
}

